<template>
  <v-skeleton-loader
    :loading="loading"
    type="article, list-item-two-line@2, actions"
  >
    <v-container>
      <v-row>
        <v-card-title class="headline" style="color: black">
          {{ $t("showOverview") }}
          <v-chip-group
            mandatory
            active-class="primary--text"
            style="margin-left: 8px"
          >
            <v-chip @click="generateChart('hourly')">{{ $t("hourly") }}</v-chip>
            <v-chip @click="generateChart('daily')">{{ $t("daily") }}</v-chip>
            <v-chip @click="generateChart('weekly')">{{ $t("weekly") }}</v-chip>
            <v-chip @click="generateChart('monthly')">{{
              $t("monthly")
            }}</v-chip>
          </v-chip-group>
        </v-card-title>
      </v-row>
      <v-row>
        <v-col cols="12" md="4">
          <v-card elevation="24" style="position: relative; min-height: 250px">
            <div style="position: absolute; top: 0px; padding: 16px">
              <v-icon color="#e5f45e" style="margin-bottom: 8px"
                >mdi-checkbox-multiple-marked-outline</v-icon
              >
              <h2 v-if="ordersSeries[0].data.length !== 0">
                {{
                  ordersSeries[0].data.reduce(
                    (r, a) => a.map((b, i) => (r[i] || 0) + b),
                    []
                  )[1]
                }}
              </h2>
              <h2 v-if="ordersSeries[0].data.length === 0">0</h2>
              <span>{{ $t("numberOfOrders") }}</span>
            </div>
            <!-- <div style="position: absolute; bottom: 0px; width: 100%" id="chart"></div> -->
            <div style="position: absolute; bottom: 0px; width: 100%">
              <apexchart
                height="150"
                type="area"
                :options="ordersChartOptions"
                :series="ordersSeries"
              ></apexchart>
            </div>
          </v-card>
        </v-col>
        <v-col cols="12" md="4">
          <v-card elevation="24" style="position: relative; min-height: 250px">
            <div style="position: absolute; top: 0px; padding: 16px">
              <v-icon color="#bfec59" style="margin-bottom: 8px"
                >mdi-checkbox-marked-outline</v-icon
              >
              <h2 v-if="ordersValueSeries[0].data.length !== 0">
                {{
                  (
                    ordersValueSeries[0].data.reduce(
                      (r, a) => a.map((b, i) => (r[i] || 0) + b),
                      []
                    )[1] /
                    ordersValueSeries[0].data.filter((e) => e[1] !== 0).length
                  ).toFixed(2)
                }}
                €
              </h2>
              <h2 v-if="ordersValueSeries[0].data.length === 0">0 €</h2>
              <span>{{ $t("averageOrderValue") }}</span>
            </div>
            <!-- <div style="position: absolute; bottom: 0px; width: 100%" id="chart2"></div> -->
            <div style="position: absolute; bottom: 0px; width: 100%">
              <apexchart
                height="150"
                type="area"
                :options="ordersValueChartOptions"
                :series="ordersValueSeries"
              ></apexchart>
            </div>
          </v-card>
        </v-col>
        <v-col cols="12" md="4">
          <v-card
            color="secondary"
            elevation="24"
            style="position: relative; min-height: 250px"
          >
            <div
              style="position: absolute; top: 0px; padding: 16px; color: white"
            >
              <v-icon color="primary" style="margin-bottom: 8px"
                >mdi-currency-usd-circle-outline</v-icon
              >
              <h2>{{ remainingRestaurantCredit.toFixed(2) }} €</h2>
              <span color="white">{{ $t("remainingCredit") }}</span>
            </div>
            <!-- <div style="position: absolute; bottom: 0px; width: 100%" id="creditChart"></div> -->
            <div style="position: absolute; bottom: 0px; width: 100%">
              <apexchart
                height="150"
                type="area"
                :options="creditChartOptions"
                :series="creditsSeries"
              ></apexchart>
            </div>
          </v-card>
        </v-col>
        <v-col cols="12">
          <v-card elevation="24" style="position: relative; min-height: 350px">
            <div style="position: absolute; top: 0px; padding: 16px">
              <v-icon color="#13e46d" style="margin-bottom: 8px"
                >mdi-currency-usd-circle-outline</v-icon
              >
              <h2 v-if="revenueSeries[0].data.length !== 0">
                {{
                  revenueSeries[0].data
                    .reduce((r, a) => a.map((b, i) => (r[i] || 0) + b), [])[1]
                    .toFixed(2)
                }}
                €
              </h2>
              <h2 v-if="revenueSeries[0].data.length === 0">0 €</h2>
              <span>{{ $t("revenue") }}</span>
            </div>
            <!-- <div style="position: absolute; bottom: 0px; width: 100%" id="chart3"></div> -->
            <div style="position: absolute; bottom: 0px; width: 100%">
              <apexchart
                height="250"
                type="area"
                :options="revenueChartOptions"
                :series="revenueSeries"
              ></apexchart>
            </div>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-skeleton-loader>
</template>

